<template>
  <div class="container">
    <h1>Products</h1>
    <div class="row" v-if="products.length > 0">
      <div class="col-lg-3">
        <div class="input-group mb-3">
          <span class="input-group-text">@</span>
          <input
            type="text"
            class="form-control"
            placeholder="Tìm kiếm"
            v-model="searchString"
          />
        </div>
        <h1>Nhóm sản phẩm</h1>
        <div class="list-group">
          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              value="sachkynang"
              id="sachkynang"
              v-model="searchCategory"
            />
            <label class="form-check-label" for="sachkynang">
              Sách Kỹ năng sống
            </label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              value="sachtamly"
              id="sachtamly"
              v-model="searchCategory"
            />
            <label class="form-check-label" for="sachtamly">
              Sách Tâm Lý
            </label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              value="sachgiaokhoa"
              id="sachgiaokhoa"
              v-model="searchCategory"
            />
            <label class="form-check-label" for="sachgiaokhoa">
              Sách Giáo Khoa
            </label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              value="truyentranh"
              id="truyentranh"
              v-model="searchCategory"
            />
            <label class="form-check-label" for="truyentranh">
              Truyện Tranh
            </label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              value="sachtienganh"
              id="sachtienganh"
              v-model="searchCategory"
            />
            <label class="form-check-label" for="sachtienganh">
              Sách Tiếng Anh
            </label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input"
              type="radio"
              value=""
              id="tatca"
              v-model="searchCategory"
            />
            <label class="form-check-label" for="tatca"> Tất cả </label>
          </div>
        </div>
      </div>
      <div class="col-lg-9 border">
        <div style="width: 200px">
          Sắp xếp theo:
          <select class="form-select" v-model="sapxepgia">
            <option value="">Hãy chọn giá</option>
            <option value="thap">Giá từ cao đến thấp</option>
            <option value="cao">Giá từ thấp đến cao</option>
          </select>
        </div>
        <div class="row">
          <div v-for="item in productFileter" :key="item.name" class="col-sm-3">
            <div class="card border-0">
              <div class="card-body">
                <router-link
                  :to="{
                    name: 'detail',
                    params: { id: item.id },
                  }"
                >
                  <img :src="item.image" alt="" class="img-thum" />
                  <h3 class="title">
                    {{ item.name }}
                  </h3>
                </router-link>
                <p class="card-text">
                  Giá:
                  <span style="color: red, font-weight: bold">
                    {{ item.price }} đ</span
                  >
                </p>
                <button class="" @click="increaseLike">
                  Số lượt thích {{ item.likeCount }}
                </button>
                <a href="#" class="btn btn-primary" @click="addCart(item)"
                  >Add to Cart</a
                >
                <button class="btn btn-info">Add to Wishlist</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>Loading...</div>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { useUser } from "@/composables/useUser";
import { useGetData } from "@/composables/useData";
import { useAddToCart } from "@/composables/addToCart";
import { computed, ref, watch } from "vue";
export default {
  setup() {
    const store = useStore();
    const { data } = useGetData();
    const { addToCart } = useAddToCart();
    const { getUser } = useUser();
    const { user } = getUser();
    var searchString = ref("");
    var searchCategory = ref("");
    var sapxepgia = ref("");

    store.dispatch("setProducts", { data: data });

    async function addCart(item) {
      await addToCart(user.value.uid, item)
        .then(() => {
          alert("Add to cart");
          let cartArrays = {};
          cartArrays = item;
          let userId = user.value.uid;
          store.state.cartOfUser.push({ cartArrays, userId });
          // store.commit("setCartforUser", user.value.uid);
        })
        .catch((error) => {
          console.log(error);
        });
    }
    const products = computed(() => store.state.products);
    const productFileter = computed(() => {
      return products.value.filter((product) => {
        if (searchCategory.value.length > 0) {
          console.log("searchCategory");
          return product.category === searchCategory.value;
        } else if (searchString.value.length >= 0) {
          console.log("search string");
          const searchConvert = searchString.value.toLowerCase();
          return product.name.toLowerCase().includes(searchConvert);
        } else {
          return products.value;
        }
      });
    });
    const sortPrice = watch(() => {
      if (sapxepgia.value == "cao") {
        return products.value.sort(
          (a, b) => parseFloat(a.price) - parseFloat(b.price)
        );
      } else if (sapxepgia.value == "thap") {
        return products.value.sort(
          (a, b) => parseFloat(b.price) - parseFloat(a.price)
        );
      } else {
        return products.value;
      }
    });

    const sortCategory = computed(() => {
      return products.value.filter((element) => {
        if (searchCategory.value.length > 0) {
          return element.category === searchCategory.value;
        } else {
          return products.value;
        }
      });
    });
    return {
      sortCategory,
      sortPrice,
      sapxepgia,
      searchString,
      searchCategory,
      data,
      addCart,
      products,
      productFileter,
    };
  },
};
</script>

<style>
.img-thum {
  width: 190px;
}
.title {
  text-decoration: none;
  font-size: 24px;
}
</style>
